*,*:before,*:after{
    box-sizing: border-box;
    text-decoration: none;
    // outline: solid 1px;
  }
  
  .fz-s{
    font-size: 14px;
    line-height: 1.5;
  }
  
  body{  
    font-family: 'Noto Sans TC', sans-serif;
  }

  a{
  display: block;
  color: white;
  text-decoration: none;
  }
    
  img{
    max-width:100%;
    height: auto;
    vertical-align: center;
  }
  p{
    font-size: 16px;
    line-height: 1.5;
  }
  h2{
    text-align: center;
    font-size: 48px;
    line-height: 72px;
    font-weight: bold;
    color: $secondary;
    margin-bottom: 48px;
    @include pad{
      font-size: 32px;
      line-height: 1.5;
      margin-bottom: 40px;
    }
    @include mobile{
      font-size: 24px;
      margin-bottom: 28px;
    }
  }
  
  
  .container{
    max-width: 1110px;
    margin: 0 auto;
    padding: 60px 0 80px 0 ;
    @include pad{
      padding: 40px 39px 56px 39px;
    }
    @include mobile{
      padding: 32px 15px 40px 15px;
    }
  }